// visually-hidden
@mixin vh() {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: none;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
}

@function rem($size) {
  @return ($size / 16)+rem;
}

// Media mixin
@mixin mw($width) {

  // @media screen and (max-width: $width + "px")
  @media screen and (max-width: $width) {
    @content;
  }
}

@mixin miw($width) {
  @media screen and (min-width: $width) {
    @content;
  }

}


@mixin mmw($max-width, $min-width) {
  @media (max-width: $max-width) and (min-width: $min-width) {
    @content;
  }
}





// Font mixin
@mixin font-load($name, $size, $weight, $style) {
  @font-face {
    src: url("/fonts/"+ $name + "-"+ $size + ".woff2") format("woff2");
    src: url("/fonts/"+ $name + "-"+ $size + ".woff") format("woff");
    font-family: $name;
    font-weight: $weight;
    font-style: $style;
    font-display: swap;
  }
}

@mixin font($name) {
  font-family: $name, "Arial", sans-serif;
}


// Position mixin
@mixin centered($axis: "both") {
  position: absolute;

  @if $axis=="both" {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  @if $axis=="x" {
    left: 50%;
    transform: translateX(-50%);
  }

  @if $axis=="y" {
    top: 50%;
    transform: translateY(-50%);
  }
}

// hover + focus
@mixin hocus($pseudo: "none") {
  @if $pseudo=="after" {

    &:hover::after,
    &:focus::after {
      @content;
    }
  }

  @else if $pseudo=="before" {

    &:hover::before,
    &:focus::before {
      @content;
    }
  }

  @else {

    &:hover,
    &:focus {
      @content;
    }
  }
}

@mixin btn-border-drawing($color: #ccc, $hover: black, $width: 2px, $vertical: top, $horizontal: left, $duration: 0.25s) {
  box-shadow: inset 0 0 0 $width $color;
  color: $hover;
  transition: color $duration $duration/3;
  position: relative;

  &::before,
  &::after {
    border: 0 solid transparent;
    box-sizing: border-box;
    content: '';
    pointer-events: none;
    position: absolute;
    width: 0;
    height: 0;

    #{$vertical}: 0;
    #{$horizontal}: 0;
  }

  &::before {
    $h-side: if($horizontal=='left', 'right', 'left');

    border-#{$vertical}-width: $width;
    border-#{$h-side}-width: $width;
  }

  &::after {
    $v-side: if($vertical=='top', 'bottom', 'top');

    border-#{$v-side}-width: $width;
    border-#{$horizontal}-width: $width;
  }

  &:hover {
    color: $hover;

    &::before,
    &::after {
      border-color: $hover;
      transition: border-color 0s, width $duration, height $duration;
      width: 100%;
      height: 100%;
    }

    &::before {
      transition-delay: 0s, 0s, $duration;
    }

    &::after {
      transition-delay: 0s, $duration, 0s;
    }
  }
}
